import React, { useState, useEffect } from 'react';
import {
  ChakraProvider,
  Box,
  Tabs,
  Tab,
  TabPanels,
  TabPanel,
  TabList,
  Button,
  theme,
  Flex,
  Text,
} from '@chakra-ui/react';
import {
  ethEnabled,
  getNetId,
} from './components/blockchain/functions/functions';
import Deployer from './components/deployer';
import Token from './components/token';
import Password from './components/password';
import { ColorModeSwitcher } from './ColorModeSwitcher';

function App() {
  const [accounts, setAccounts] = useState('');
  const [netId, setNetId] = useState({
    id: '',
    name: '',
  });

  const handleLogin = async () => {
    let user = await ethEnabled();
    getNetwork();
    setAccounts(user[0]);
    console.log(accounts);
  };

  const getNetwork = async () => {
    let network = await getNetId();

    setNetId(network);
  };

  useEffect(() => {
    window.ethereum.on('networkChanged', getNetwork);
  }, []);

  return (
    <ChakraProvider theme={theme}>
      <Box textAlign="center" fontSize="xl">
        <Flex justifyContent="space-between">
          <ColorModeSwitcher />
          {netId.name && (
            <Text>
              {netId.name} ({netId.id})
            </Text>
          )}

          <Button onClick={handleLogin}>
            {accounts
              ? `${accounts?.substring(0, 10)}...${accounts?.substring(36, 42)}`
              : 'Connect'}
          </Button>
        </Flex>

        <Tabs isFitted variant="enclosed">
          <TabList mb="1em">
            <Tab>Deployer</Tab>
            <Tab>Token</Tab>
            <Tab>Password</Tab>
          </TabList>
          <TabPanels>
            <TabPanel>
              <Deployer />
            </TabPanel>
            <TabPanel>
              <Token />
            </TabPanel>
            <TabPanel>
              <Password />
            </TabPanel>
          </TabPanels>
        </Tabs>
      </Box>
    </ChakraProvider>
  );
}

export default App;
